import * as React from "react"
import Draggable from 'react-draggable'; // The default
import "xp.css/dist/98.css";

// markup
//      <button aria-label="Minimize"></button>
//      <button aria-label="Maximize"></button>
//

const IndexPage = (props) => {
  const close = () => setShowWindow(false)
  const [showWindow, setShowWindow] = React.useState(true)
  const close2 = () => setShowWindow(false)
  const [showWindow2, setShowWindow2] = React.useState(true)
  return (
    <div>
    { false ? <Window handler={close} /> : null }
    { showWindow2 ? <CommandPrompt handler={close2} /> : null }
    </div>
  )
}
const Window = (props) => (
    <Draggable>
    <div class="window" style={{width: 300}}>
      <div class="title-bar">
        <div class="title-bar-text">A Window With Stuff In It</div>
        <div class="title-bar-controls">
          <button aria-label="Close" onClick={props.handler}></button>
        </div>
      </div>
      <div class="window-body">
        <p>There's so much room for activities!</p>
      </div>
    </div>
    </Draggable>
    )

const CommandPrompt = (props) => (
  <Draggable>
    <div class="window">
    <div class="title-bar">
      <div class="title-bar-text">Command Prompt</div>
      <div class="title-bar-controls">
        <button aria-label="Minimize"></button>
        <button aria-label="Maximize"></button>
        <button aria-label="Close" onClick={props.handler}></button>
      </div>
    </div>
    <div class="window-body">
      <pre>
      Microsoft Windows [Version 10.0.19041.685]<br />
(c) 2020 Microsoft Corporation. All rights reserved.<br/>
        Under construction!
      </pre>
    </div>
  </div>
  </Draggable>

)


export default IndexPage
